.App {
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #242526;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: start;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #ffffff;
}

.navbar {
  width: 100%;
  position: fixed;
  top: 0;
  background-color: #3a3b3c;
  color: white;
}

.content {
  margin-top: 1rem;
  margin-left: 6rem;
  margin-right: 6rem;
}

h3 {
  margin-bottom: 3rem;
}

.notice {
  margin-top: 9rem;
  margin-left: 6rem;
  margin-right: 6rem;
  color: red;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
